/* global Waypoint TimelineLite */
/* eslint-disable */

var done = false;
function onDone() {
	done = true;
}

var t1 = new TimelineLite();
var ease = window.Power4.easeInOut;

var waypoint1 = new Waypoint({ // eslint-disable-line
	element: document.querySelector(".q-subscription__try"),
	offset: "100%",

	handler: function handler(direction) {
		if (direction === "down" && !done) {
			t1.addLabel("start"); // start time = 0
			t1.fromTo(".q-subscription__try", 0.74, { y: "50px", autoAlpha: 0 }, { y: "0", autoAlpha: 1, ease: ease, onComplete: onDone }, "start+=0.85");
			t1.fromTo(".try-left-column", 0.74, { y: "30px", autoAlpha: 0 }, { y: "0", autoAlpha: 1, ease: ease, onComplete: onDone }, "start+=1.15");
			t1.fromTo(".try-right-column", 0.74, { y: "30px", autoAlpha: 0 }, { y: "0", autoAlpha: 1, ease: ease, onComplete: onDone }, "start+=1.25");
			t1.fromTo(".q-slab-top", 0.74, { y: "-30px", autoAlpha: 0 }, { y: "0", autoAlpha: 1, ease: ease , onComplete: onDone }, "start+=1.55");
			t1.fromTo(".q-slab-middle", 0.74, { y: "-30px", autoAlpha: 0 }, { y: "0", autoAlpha: 1, ease: ease, onComplete: onDone }, "start+=1.45");
			t1.fromTo(".q-slab-bottom", 0.74, { y: "-30px", autoAlpha: 0 }, { y: "0", autoAlpha: 1, ease: ease, onComplete: onDone }, "start+=1.35");
		}
	}
});

/**
 * slide down/up functions
 */
let slideUp = (target, duration=500) => {

	target.style.height = target.offsetHeight + 'px';
	target.offsetHeight;
	target.style.overflow = 'hidden';
	target.style.height = 0;
	window.setTimeout( () => {
		target.style.display = 'none';
		target.style.removeProperty('height');
		target.style.removeProperty('overflow');
	}, duration);
}

let slideDown = (target, duration=500) => {
	target.style.removeProperty('display');
	let display = window.getComputedStyle(target).display;

	if (display === 'none')
		display = 'block';

	target.style.display = display;
	let height = target.offsetHeight;
	target.style.overflow = 'hidden';
	target.style.height = 0;
	target.offsetHeight;
	target.style.height = height + 'px';
	window.setTimeout( () => {
		target.style.removeProperty('height');
		target.style.removeProperty('overflow');
	}, duration);
}

var slideToggle = (target, duration = 500) => {
	if (window.getComputedStyle(target).display === 'none') {
		return slideDown(target, duration);
	} else {
		return slideUp(target, duration);
	}
}

// Toggle cloud platform accordion

(function () {

	"use strict";

	var accordion = document.querySelectorAll(".q-subscriptions__options-handle");

	Array.prototype.forEach.call(accordion, function(el){
		// console.log( el.nextElementSibling );
		el.addEventListener("click", function(){

			// toggle the minus class
			el.classList.toggle("active");
			slideToggle(el.nextElementSibling, 200);
		});
	});

}());

